.infoBox{
    flex: 1;
    cursor: pointer;
}
.infoBox:not(:last-child){
    margin-right: 15px;
}
.infobox_cases{
    color: #cc1034;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;


}
.infoBox--red{
    border-color: red;
}
.infoBox--selected{
    border-top: 10px solid greenyellow;
    background-image: url("https://www.google.com/url?sa=i&url=https%3A%2F%2Fpngtree.com%2Ffreebackground%2F3d-effects-background-material_395164.html&psig=AOvVaw1TwK6wDebS3xO3L-T7FhjW&ust=1603808610070000&source=images&cd=vfe&ved=0CAIQjRxqFwoTCNjvweu60uwCFQAAAAAdAAAAABAD");
}
.infoBox_total{
    color: #6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
}